@import url(https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@500;700;900&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

 body{
  background-image: url(/static/media/pattern-background-desktop.5a0ab689.svg);
  background-size: contain;
  background-repeat: no-repeat;
  background-color: hsl(225, 100%, 94%);
  display: flex;
  font-family: 'Red Hat Display', sans-serif;
  font-size: 16px;
  justify-content: center;
} 

.wrapper{
  max-width: 1440px;
}







.order-card{
  margin:  0 auto;
  margin-top: 100px;
  width: 450px;
  background-color: #fff;
  border-radius: 20px;
  box-shadow:  0px 4px 15px 0px rgba(0, 0, 0, 0.1)
}

.card-content{
  padding: 30px 20px;
  text-align: center;
  
}

.order-card__image{
  width: 450px;
  height: 220px;
  background-image: url(/static/media/illustration-hero.73032583.svg);
  border-radius: 20px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

h2{
  font-size: 28px;
  font-weight:  700;
  text-align: center;
}

p{
  padding: 20px 30px ;
  color: hsl(224, 23%, 55%);
}


.plan{
  background-color: hsl(225, 100%, 98%);
  border-radius: 10px;
  padding: 25px 20px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 0 30px 30px 30px;
}

.plan-image{
  background-image: url(/static/media/icon-music.f20c7787.svg);
  width: 48px;
  height: 48px;
  margin-right: 20px;
}


.plan-text{
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.plan-text h4{
  margin-bottom: 4px;
}

.plan-text p{
  padding: 0;
}
.plan-button{
  text-decoration: none;
  color: hsl(245, 75%, 52%);
  margin-left: auto;
}

.proceed-button{
  background-color: hsla(245, 78%, 61%, 0.932) ;
  color: hsl(225, 100%, 98%);
  display: block;
  text-decoration: none;
  margin: 0 30px 30px 30px;
  border-radius: 10px;
  padding: 15px 20px;
  box-shadow: 0px 5px 15px  hsl(224, 23%, 55%);
}

.cancel-order{
  text-decoration: none;
  color: black;
  cursor: pointer;
}


@media only screen and (max-width: 575px) {
  body {
    background-image: url(/static/media/pattern-background-mobile.c8d50fcb.svg);
    background-size: contain;
    background-repeat: no-repeat;
    background-color: hsl(225, 100%, 94%);
  }
}
